.Business {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 16.66rem;
  margin: 0 .5rem 2.3rem .5rem;
  color: #979797;
}
@media only screen and (max-width: 560px) {
  .Business {
    width: 40rem;
  }
}
.Business .image-container {
  height: 12.66rem;
  margin-bottom: 0;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
  -webkit-box-shadow: 2px 26px 74px -17px rgba(0,0,0,0.47);
  -moz-box-shadow: 2px 26px 74px -17px rgba(0,0,0,0.47);
  box-shadow: 0px 50px 40px -30px rgba(0,0,0,0.15);
}
.Business .image-container img {
  width: 140%;
  margin: -20%;
}
.business-card {
  margin: 1.3rem 0;
}
.business-name {
  font-family: ff-dagny-web-pro, sans-serif;
  font-weight: 700;
  color: #181818;
  margin: .2rem 0;
  font-size: 1rem;
}
.Business-information p {
  font-size: .88rem;
  font-weight: 300;
  margin: 0;
}
.Business-address {
  flex-direction: column;
  justify-content: space-between;
}
.Business-address p {
  margin: 0px;
  padding: 0px;
  font-size: .8rem;
  color: #B5B5B6;
}
.Business-information {
  padding: .5rem 0;
}
.Business-reviews .rating {
  padding: 0;
  margin: 0;
}
.review {
  color: #696969;
  font-size: .77rem;
  font-weight: 400;
  padding: 0;
  margin: 0;
  line-height: .6rem;
}
.food-category {
  font-size: .9rem;
  color: #0a0a0a;
  background: #FFF;
  padding: .1rem .5rem;
  margin: .5rem;
  bottom: 0;
  right: 0;
  position: absolute;
  border-radius: 15px;
  -webkit-box-shadow: 2px 26px 74px -17px rgba(0,0,0,0.47);
  -moz-box-shadow: 2px 26px 74px -17px rgba(0,0,0,0.47);
  box-shadow: 0px 5px 20px 0px rgba(0,0,0,0.97);
}
