.PageHeader {
  padding: 1rem 0 3rem;
}
.searchBar-searched .PageHeader {
    padding: .5rem;
}
.SiteLogo {
  background: url('./devour-logo.png') no-repeat;
  display: block;
  height: 84px;
  width: 257px;
  margin: 0 auto;
}
.SearchBar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-image: url('./background_search_desktop.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
}
.searchBar-searched {
  height: 25rem;
}

.SearchBar-sort-options ul {
  display: flex;
  justify-content: center;
  color: #ffffff;
  padding: 0;
  margin: 1rem auto;
}
.SearchBar-sort-options li {
  font-family:  ff-dagny-web-pro, sans-serif;
  cursor: pointer;
  width: 8rem;
  padding: .1rem .5rem;
  margin: 0;
  line-height: 2.2em;
  text-align: center;
  font-weight: 700;
  font-size: .83rem;
  transition: color .25s;
  list-style: none;
  opacity: .5;
}
@media only screen and (max-width: 560px) {
  .SearchBar-sort-options li {
    margin: 0;
    width: 8rem;
    line-height: 1em;
  }
}
.SearchBar-sort-options li:hover {
  color: #2ef46b;
  opacity: 1;
}
.SearchBar-sort-options li.active,
.SearchBar-sort-options li.active:hover {
  border-radius: 20px;
  background-color: #2ef46b;
  border: 2px solid #2ef46b;
  color: #000;
  opacity: 1;
  line-height: 2.2em;
}
@media only screen and (max-width: 560px) {
  .SearchBar-sort-options li.active,
  .SearchBar-sort-options li.active:hover {
    line-height: 1em;
  }
}
.SearchBar-fields {
  display: flex;
  width: 22rem;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
}
.SearchBar-fields input {
  font-family:  ff-dagny-web-pro, sans-serif;
  color: #FFF;
  width: 21rem;
  padding: .2rem 1rem;
  border: none;
  border-radius: 0px;
  font-size: 2rem;
  font-weight: 700;
  background: rgba(0, 0, 0, 0);
  text-align: center;
}
.SearchBar-fields input::placeholder {
  color: #cbcbcb;
  font-size: 1.5rem;
}
.SearchBar-fields p {
  font-family:  ff-dagny-web-pro, sans-serif;
  font-weight: 600;
  color: #FFF;
  padding: 0;
  margin: 0;
}
.SearchBar-sortBy-headline {
  margin: 0 auto;
  padding: 0;
  width: 10rem;
  text-align: center;
  font-family: ff-dagny-web-pro, sans-serif;
  font-weight: 600;
  color: #FFF;
}
.SearchBar-submit {
  text-align: center;
}
.SearchBar-submit-button {
  font-family: ff-dagny-web-pro, sans-serif;
  border-radius: 25px;
  padding: .50rem 1.7rem;
  margin: .7rem 0;
  border: 3px solid #2ef46b;
  color: #ffffff;
  font-weight: 600;
  transition: background-color .5s;
  text-decoration: none;
  display: inline-block;
}
.SearchBar-submit-button:hover {
  text-decoration: none;
  cursor: pointer;
  background-color: #2ef46b;
}
.submit-error,
.submit-error:hover {
  border-radius: 25px;
  padding: .50rem 1.7rem;
  margin-bottom: .7rem;
  border: 3px solid #ED321A;
  color: #ED321A;
  font-weight: 600;
  transition: background-color .5s;
  text-decoration: none;
  display: inline-block;
}
@media only screen and (max-width: 560px) {
  .SearchBar {
    background-image: url('./background_search_mobile.jpg');
  }

  .SearchBar-sort-options ul {
    margin-left: 2rem;
    margin-right: 2rem;
  }

  .SearchBar-fields {
    flex-direction: column;
    align-items: center;
  }

  .SearchBar-fields input {
    margin-right: 0;
    margin-bottom: .86rem;
  }
}
